*,
*::before,
*::after {
    box-sizing: border-box;
}

/* Custom Properties */
:root {
  --ff-primary: 'Roboto Slab', serif;
  --ff-secondary:'Shrikhand', monospace;

  --fw-reg: 300;
  --fw-bold: 900;

  --clr-primary: #112A38;
  --clr-accent: #74A2A6;
  --clr-dark: #0D1B1E;
  --clr-light: #FFFFFF;
  --clr-button-text: #0D1B1E;
  --clr-alt-text: black;
  --clr-shadow: #0D1B1E;


  --fs-h1: 3rem;
  --fs-h2: 2.25rem;
  --fs-h3: 1.25rem;
  --fs-body: 1rem;

  --bs: 1.875rem 1.875rem 0 var(--clr-shadow);
}

.dark-mode {
  --clr-primary: #112A38;
  --clr-accent: #74A2A6;
  --clr-dark: #0D1B1E;
  --clr-light: #FFFFFF;
  --clr-button-text: #0D1B1E;
  --clr-alt-text: black;
  --clr-shadow: #0D1B1E;
}

.light-mode {
  --clr-primary: #FFFFFF;
  --clr-accent: #276250;
  --clr-dark: #74A2A6;
  --clr-light: #0D1B1E;
  --clr-button-text: white;
  --clr-alt-text: white;
  --clr-shadow: #034C37;
}

html {
  scroll-behavior: smooth;
}

/* General Styles */

body {
  position: relative;
  margin: 0;
  padding: 0;
  min-height: 100%;
  height: 100%;
  max-width: 100vw;
  background: #112A38;
  background: var(--clr-primary);
  color: #FFFFFF;
  color: var(--clr-light);
  font-family: 'Roboto Slab', serif;
  font-family: var(--ff-primary);
  font-size: 1rem;
  font-size: var(--fs-body);
  line-height: 1.6;
}

.App {
  text-align: center;
}

#bg {
  position: absolute;
  margin: 0;
  padding: 0;
  bottom: 0;
  top: -4.75rem;
  z-index: -1;
  overflow: hidden;
  width: 17%;
}

section {
  height: 100vh;
  margin-top: 6rem;
  scroll-margin-top: 6rem;
}

.section--header {
  display: flex;
  align-items: center;
  justify-content: center;
  grid-gap: 2rem;
  gap: 2rem;
}

.icon {
  display: inline-block;
  width: 2rem;
  color: #74A2A6;
  color: var(--clr-accent);
}

.header--underline {
  display: block;
  margin: auto;
  margin-top: 0;
  border: none;
  border-bottom: 0.188rem solid #74A2A6;
  border-bottom: 0.188rem solid var(--clr-accent);
  width: 14.5rem;
}

.header--underline::after {
  content: '';
  border: none;
  border-bottom: 0.23rem solid #0D1B1E;
  border-bottom: 0.23rem solid var(--clr-dark);
  position: absolute;
  width: 14.5rem;
  margin-top: 0.15rem;
  margin-left: -6.25rem;
}

.card--container {
  display: flex;
  flex-direction: column;
  align-items: center;
  object-position: center;
  text-align: center;
}

.card--style {
  margin-top: 3.4375rem;
  margin-bottom: 3.4375rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 65%;
  max-height: 40.625rem;
  border-radius: 1.5625rem;
  background-color: #74A2A6;
  background-color: var(--clr-accent);
  color: black;
  color: var(--clr-alt-text);
  box-shadow: 1.875rem 1.875rem 0 #0D1B1E;
  box-shadow: var(--bs);
}

.card--style > ul {
  padding-left: 3.4375rem;
  padding-right: 3.4375rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  list-style: none;
}

.card--style > p {
  text-align: center;
  width: 53.6875rem;
  font-size: 1.875rem;
  line-height: 2.25rem;
}

/* Navigation Bar */

.nav {
  position: fixed;
  z-index: 2;
  top: 0;
  height: 4.75rem;
  width: 100%;
  padding: 0 3.125rem 0 1.5625rem;
  display: flex;
  align-items: center;
  justify-content: center;
  justify-content: space-between;
  height: 4.6875rem;
  background-color: #0D1B1E;
  background-color: var(--clr-dark);
  color: #FFFFFF;
  color: var(--clr-light);
}

.nav__author {
  font-size: 1.375rem;
  letter-spacing: 0.0625rem;
}

.nav__author > a:hover {
  display: inline-block;
  color: #74A2A6;
  color: var(--clr-accent);
  -webkit-transform: scale(1.25);
          transform: scale(1.25);
  opacity: .75;
}

.nav__author > a {
  color: #FFFFFF;
  color: var(--clr-light);
  text-decoration: none;
}

.nav__links {
  display: flex;
  align-items: center;
  justify-content: center;
  grid-gap: 3.4375rem;
  gap: 3.4375rem;
  font-size: 1.125rem;
  line-height: 1.375rem;
}

.nav__links > li {
  list-style: none;
  width: 4.6875rem;
}

.nav--default {
  color: #FFFFFF;
  color: var(--clr-light);
  text-decoration: none;
}

.nav__links > li > a:hover,
.nav--active {
  color: #74A2A6;
  color: var(--clr-accent);
  font-size: 1.375rem;
  text-decoration: underline;
}

/* Intro */

.intro {
  /* margin-top: 6rem; */
  height: 100vh;
  max-width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.intro--container {
  display: flex;
}

.intro--description {
  display: flex;
  padding-left: 15rem;
  flex-direction: column;
  width: 85%;
  text-align: left;
  justify-content: center;
}

.intro--underline {
  text-decoration: underline;
  text-decoration-thickness: 0.15rem;
  -webkit-text-decoration-color: #74A2A6;
          text-decoration-color: #74A2A6;
  -webkit-text-decoration-color: var(--clr-accent);
          text-decoration-color: var(--clr-accent);
  margin: auto;
}

.intro--container > img {
  width: 40%;
}

.intro--buttons {
  display: flex;
  grid-gap: 2rem;
  gap: 2rem;
  padding-bottom: 2.5rem;
}

.intro--buttons > button {
  height: 2.5rem;
  background-color: #74A2A6;
  background-color: var(--clr-accent);
  border: none;
  /* width: 9rem; */
  border-radius: 0.5rem;
}

.intro--buttons > button:hover {
  display: inline-block;
  color: #74A2A6;
  color: var(--clr-accent);
  -webkit-transform: scale(1.25);
          transform: scale(1.25);
  opacity: .75;
  box-shadow: 0.4rem 0.4rem 0 #0D1B1E;
  box-shadow: 0.4rem 0.4rem 0 var(--clr-dark);
}

.intro--buttons > button > a {
  font-size: 1.25rem;
  font-size: var(--fs-h3);
  font-weight: 900;
  font-weight: var(--fw-bold);
  cursor: pointer;
  text-decoration: none;
  color: #0D1B1E;
  color: var(--clr-button-text);
}

/* Project Styles */

#project {
  height: auto;
}

.project--container {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  grid-gap: 4rem;
  gap: 4rem;
  margin-top: 2rem;
  margin-bottom: 2rem;
}

.project--container > a {
  text-decoration: none;
  color: #74A2A6;
  color: var(--clr-accent);
}

.project--card {
  position: relative;
  display: flex;
  z-index: 1;
  width: 50rem;
  height: 14rem;
  border-radius: 25px;
  background-color: #112A38;
  background-color: var(--clr-primary);
  box-shadow: 1.875rem 1.875rem 0 #0D1B1E,
    inset 0px 0px 0px 2px #0D1B1E;
  box-shadow: var(--bs),
    inset 0px 0px 0px 2px var(--clr-shadow);
  overflow: hidden;
}

.project--front {
  position: absolute;
  display: flex;
  width: 100%;
  height: 100%;
}

.project--back {
  display: flex;
  width: 100%;
  opacity: 0;
  transition: opacity 1s ease-out;
}

.project--card:hover > .project--back {
  opacity: 1;
  transition: opacity 1s ease-in;
}

.project--card:hover > .project--front {
  opacity: 0;
  transition: opacity 1s ease-in;
}

.project--img {
  max-width: 50%;
  width: 100%;
  object-fit: cover;
  border-radius: 1.5625rem;
  border: 2px solid #0D1B1E;
  border: 2px solid var(--clr-shadow);
}

.project--svg {
  padding-top: 1rem;
  padding-bottom: 0.5rem;
  width: 100%;
}

.project--text {
  display: flex;
  flex-direction: column;
  width: 50%;
  height: 100%;
  padding: 2px;
  word-wrap: break-word;
  background-color: #0D1B1E;
  background-color: var(--clr-dark);
  box-shadow: inset 0px 0px 0px 2px #0D1B1E;
  box-shadow: inset 0px 0px 0px 2px var(--clr-shadow);
  border-radius: 25px;
  line-height: 1em;
}

.project--text > p {
  padding-top: 0.5rem;
  font-size: 1.25rem;
  line-height: 1.5em;
}

.project--skills {
  display: flex;
  flex-direction: column;
  width: 50%;
  /* gap: 2rem; */
  justify-content: center;
  align-items: center;
}

.project--skills > img {
  height: 50%;
}

.reverse {
  flex-direction: row-reverse;
}

.contact--container {
  display: flex;
  justify-content: center !important;
  grid-gap: 2rem;
  gap: 2rem;
}

.contact--icon {
  width: 5rem;
  color: #0D1B1E;
  color: var(--clr-dark);
  height: 3rem;
}


/*  Media Queries */

/* smartphones, iPhone, portrait 480x320 phones */
@media (min-width:320px)  {  
  .intro {
    width: 100vw;
    align-items: center;
    padding: 0;
  }
  .intro--container {
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  .intro--description {
    padding: 0;
    margin-left: 2rem;
  }
  .intro--container > img {
    width: 60%;
    align-self: center;
    margin: 0;
    padding: 0;
  }

  .project--container {
    margin-left: 1rem;
    margin-right: 1rem;
    align-items: stretch;
  }

  .project--card {
    flex-direction: column;
    height: 28rem;
    min-width: 100%;
    width: 100%;
  }

  .project--front {
    flex-direction: column;
    height: 100%;
  }

  .project--back {
    flex-direction: column;
    height: 100%;
  }

  .project--img {
    max-height: 100%;
    max-width: 100%;
    height: 50%;
    width: 100%;
  }

  .project--svg {
    max-width: 100%;
    max-height: 50%;
  }

  .project--text {
    height: 100%;
    width: 100%;
  }

  .project--skills {
    height: 100%;
    width: 100%;
  }

  .card--container {
    width: 100%;
    padding-left: 1rem;
    padding-right: 1rem;
  }

  .card--style {
    width: 100%;
  }

  .card--style > ul {
    width: 100%;
  }

  .card--style > p {
    text-align: center;
    width: 100%;
  }

}
@media (min-width:481px)  { /* portrait e-readers (Nook/Kindle), smaller tablets @ 600 or @ 640 wide. */ }
@media (min-width:641px)  { /* portrait tablets, portrait iPad, landscape e-readers, landscape 800x480 or 854x480 phones */ }

/* tablet, landscape iPad, lo-res laptops ands desktops */ 
@media (min-width:961px)  { 

  .intro--container > img {
    width: 40%;
  }

  .intro--container {
    flex-direction: row;
  }

  .project--container {
    margin-left: initial;
    margin-right: initial;
    align-items: center;
  }

  .project--card {
    height: 14rem;
    width: 50rem;
  }

  .project--front {
    flex-direction: row;
    width: 100%;
    height: 100%;
  }

  .project--back {
    flex-direction: row;
    height: initial;
    width: 100%;
    height: 100%;
  }

  .project--img {
    max-height: initial;
    max-width: 50%;
    height: initial;
    width: 100%;
  }

  .project--svg {
    max-width: initial;
    max-height: initial;
    width: 100%;
  }

  .project--text {
    height: initial;
    width: 100%;
  }

  .project--skills {
    height: initial;
    width: 50%;
  }
  
  .reverse {
    flex-direction: row-reverse;
  }

  .card--container {
    width: 100%;
    padding-left: initial;
    padding-right: initial;
  }

  .card--style {
    width: 65%;
  }

  .card--style > ul {
    width: 100%;
  }

  .card--style > p {
    text-align: center;
    width: 53.6875rem;
  }
}
@media (min-width:1025px) { /* big landscape tablets, laptops, and desktops */ }
@media (min-width:1281px) { /* hi-res laptops and desktops */ }

:root {
    /** sunny side **/
    --blue-background: #c2e9f6;
    --blue-border: #276250;
    --blue-color: #96dcee;
    --yellow-background: #ede895;
    --yellow-border: #c7c275;
    /** dark side **/
    --indigo-background: #74A2A6;
    --indigo-border: #74A2A6;
    --indigo-color: #112A38;
    --gray-border: #74A2A6;
    --gray-dots: #112A38;
}

@-webkit-keyframes reverse {
    0% {
        left: 47px;
        width: 40px;
    }
    60% {
        left: 3px;
        width: 80px;
    }
    100% {
        left: 3px;
    }
}

@keyframes reverse {
    0% {
        left: 47px;
        width: 40px;
    }
    60% {
        left: 3px;
        width: 80px;
    }
    100% {
        left: 3px;
    }
}
    
@-webkit-keyframes switch {
    0% {
        left: 3px;
    }
    60% {
        left: 3px;
        width: 80px;
    }
    100% {
        left: 47px;
        width: 40px;
    }
}
    
@keyframes switch {
    0% {
        left: 3px;
    }
    60% {
        left: 3px;
        width: 80px;
    }
    100% {
        left: 47px;
        width: 40px;
    }
}

.toggle--checkbox {
    opacity: 0;
}

.toggle--checkbox:focus + .toggle--label {
    outline: solid 3px var(--button-border);
    transition: outline 100ms ease-in;
}

.container--toggle {
    display: flex;
    place-items: center;
    min-height: 60px;
    position: relative;
    grid-area: toggle;
}

/* background */
.toggle--label {
    width: 100px;
    height: 48px;
    background: #112A38;
    background: var(--indigo-color);
    border-radius: 100px;
    border: 3px solid #74A2A6;
    border: 3px solid var(--indigo-border);
    display: flex;
    position: relative;
    transition: all 350ms ease-in;
    color: #112A38;
    color: var(--indigo-color);
}

.toggle--checkbox:checked + .toggle--label {
    background: #96dcee;
    background: var(--blue-color);
    color: #96dcee;
    color: var(--blue-color);
    border-color: #276250;
    border-color: var(--blue-border);
}

/* sun and moon */
.toggle--checkbox:checked + .toggle--label:before {
    -webkit-animation-name: reverse;
            animation-name: reverse;
    -webkit-animation-duration: 350ms;
            animation-duration: 350ms;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    transition: all 360ms ease-in;
    background: #ede895;
    background: var(--yellow-background);
    border-color: #c7c275;
    border-color: var(--yellow-border);
}

.toggle--label:before {
    -webkit-animation-name: switch;
            animation-name: switch;
    -webkit-animation-duration: 350ms;
            animation-duration: 350ms;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    content: '';
    width: 40px;
    height: 40px;
    border: 3px solid #74A2A6;
    border: 3px solid var(--gray-border);
    top: 1px;
    left: 3px;
    position: absolute;
    border-radius: 40px;
    background: #74A2A6;
}

/* moon dimples */
.toggle--label:after {
    transition-delay: 0ms;
    transition: all 250ms ease-in;
    position: absolute;
    content: '';
    box-shadow: #112A38 -14px 0 0 1px, #112A38 -22px 12px 0 -1px;
    box-shadow: var(--gray-dots) -14px 0 0 1px, var(--gray-dots) -22px 12px 0 -1px;
    left: 80px;
    top: 10px;
    width: 6px;
    height: 6px;
    background: transparent;
    border-radius: 50%;
    opacity: 1;
}

.toggle--checkbox:checked + .toggle--label:after {
    transition-delay: 50ms;
    opacity: 0;
}

/* clouds */
.toggle--checkbox:checked + .toggle--label .toggle--label-background {
    width: 5px;
    left: 65px;
    top: 22px;
}

.toggle--checkbox:checked + .toggle--label .toggle--label-background:before {
    top: -4px;
    left: -5px;
    width: 20px;
    height: 5px;
}

.toggle--checkbox:checked + .toggle--label .toggle--label-background:after {
    top: 3px;
    width: 20px;
    height: 5px;
    left: -10px;
}

/* stars */
.toggle--label-background {
    border-radius: 5px;
    position: relative;
    background: white;
    left: 32px;
    width: 6px;
    transition: all 150ms ease-in;
    top: 25px;
    height: 4px;
}

.toggle--label-background:before {
    content: '';
    position: absolute;
    width: 4px;
    height: 4px;
    top: -15px;
    border-radius: 5px;
    background: white;
    left: -20px;
    transition: all 150ms ease-in;
}

.toggle--label-background:after {
    content: '';
    position: absolute;
    width: 4px;
    height: 4px;
    left: -20px;
    top: 10px;
    border-radius: 5px;
    background: white;
    transition: all 150ms ease-in;
}
.dome-container {
    display: inline-block;
    width: 30vw;
    height: 30vh;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 2rem;
}

.dome {
    --r: 20vmin;
    /* circle radius */
    --t: 3;
    /* number of skills */
    position: relative;
    --w: calc(var(--r) * 1.2);
    width: calc(2 * var(--w));
    height: calc(2 * var(--w));
    margin: 0;
    padding: 0;
    -webkit-animation: rotate 10s linear infinite;
            animation: rotate 10s linear infinite;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
}

@-webkit-keyframes rotate {
    0% {
        -webkit-transform: rotate(0);
                transform: rotate(0);
    }
    100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes rotate {
    0% {
        -webkit-transform: rotate(0);
                transform: rotate(0);
    }
    100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

.dome::before {
    content: '';
    width: calc(2 * var(--r));
    aspect-ratio: 1 / 1;
    border-radius: 50%;
    position: absolute;
    top: calc(var(--r) * 0.2);
    left: calc(var(--r) * 0.2);
    display: inline-block;
    margin: 0;
    padding: 0;
}

.skill {
    height: calc(var(--r) * 1.2);
    width: calc(var(--r) / 5);
    position: absolute;
    bottom: var(--w);
    left: calc(var(--w) - (0.5 * var(--r) / 5));
    -webkit-transform-origin: center bottom;
            transform-origin: center bottom;
    -webkit-transform: rotate(calc(var(--n) * 22.5deg));
            transform: rotate(calc(var(--n) * 22.5deg));
    position: absolute;
    background-size: contain;
    background-position: 0 0;
    background-repeat: no-repeat;
    margin: 0;
    padding: 0;
}

.skill:nth-child(1) {
    --n: 1;
}
  
.skill:nth-child(2) {
    --n: 4;
}
  
.skill:nth-child(3) {
    --n: 7;
}

.skill:nth-child(4) {
    --n: 10;
} 

.skill:nth-child(5) {
    --n: 14;
}

@media (min-width:320px)  { 
    .skill {
        height: calc(var(--r) * 1.2);
        width: calc(var(--r) / 2.5);
    }
}


@media (min-width:961px)  { 
    .skill {
        height: calc(var(--r) * 1.2);
        width: calc(var(--r) / 3.5);
    }
}
