  width: 193px;
  height: 52px;
  border-radius: 15px;
  border-style: none;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;
  cursor: pointer;
}

.btn-style:active {
  -webkit-transform: scale(0.98);
          transform: scale(0.98);
  box-shadow: 3px 2px 22px 1px rgba(0, 0, 0, 0.24);
}

.quiz--container {
  margin: 75px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.quiz--box {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.quiz--question {
  display: flex;
  margin: 40px 30% 25px 30%;
  width: 800px;
  color: #293264;
  /* width: 100vw; */
  font-family: 'Karla';
  font-style: normal;
  font-weight: 700;
  font-size: 24px;
  line-height: 26px;
}

.quiz--answers {
  display: flex;
  width: 800px;
  /* justify-content: space-between; */
  grid-gap: 25px;
  gap: 25px;
  
  padding-bottom: 30px;
  border-bottom: 0.8px solid #DBDEF0;
}

.quiz--answers > input, button {
  background: #D6DBF5;  
  color: #293264;
  min-height: 30px;
  /* width: 65px; */

  border-radius: 8px;
  border-style: none;
  font-family: 'Inter';
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 12px;
  max-width: 150px;

  white-space: normal;
  word-wrap: break-word;
}

.quiz--answers > input:active {
  -webkit-transform: scale(0.95);
          transform: scale(0.95);
  box-shadow: 3px 2px 22px 1px rgba(0, 0, 0, 0.24);
}

.quiz--end {
  display: flex;
  margin-top: 30px;
  align-items: center;
}

.quiz--end > h1 {
  width: 400px;
  height: 15px;
  font-family: 'Inter';
  font-style: normal;
  font-weight: 700;
  font-size: 20px;
  line-height: 15px;
  text-align: center;  
  color: #293264;
}

.btn--final {
  background-color: #4D5B9E;
  border-radius: 10px;
  width: 140px;
  height: 40px;

  color: #F5F7FB;
  font-family: 'Inter';
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 12px;
  text-align: center;
}

.btn--final:active {
  -webkit-transform: scale(0.95);
          transform: scale(0.95);
  box-shadow: 3px 2px 22px 1px rgba(0, 0, 0, 0.24);
}
